.inputContainer {
    display: flex;
    flex-direction: column;

}

.superInput {
    /*flex-grow:1;*/
    outline: none;
    border:none;
    background:#f7f1f1;
    padding:0 1.5em;
    max-width: 150px;
    height: 2rem;
    font-family:'Quicksand', sans-serif;
    margin-bottom: 10px;
}

.superInput:focus{
    outline: none;
    border: 2px solid #330867;
}

.errorInput {
    /*flex-grow:1;*/
    background:#f7f1f1;
    padding:0 1.5em;
    max-width: 150px;
    border: 2px solid red;
}

.error {
    color: red;
    height: 25px;
    margin-left: 10px;
}